.SignUp {
    font-family: sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
    background-color: #ffffff;
  }
  
  input[type="text"],
  input[type="date"],
  input[type="password"] {
    margin-left: auto;
    height: 25px;
    border: 1px solid rgba(0, 0, 0, 0.2);
  }
  
  input[type="submit"] {
    margin-top: 10px;
    cursor: pointer;
    font-size: 15px;
    background: #ca3c2a;
    border: 1px solid rgba(163, 200, 254, 0.82);
    color: #fff;
    padding: 10px 20px;
  }
  
  input[type="submit"]:hover {
    background: rgba(67, 109, 200, 0.73);
  }
  
  .button-container {
    display: flex;
    justify-content: center;
    border-radius: 20px;
  }
  
  .SignUp-form {
    background-color: rgb(89, 81, 81);
    padding: 2rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: #ffffff;
  }
  
  .list-container {
    display: flex;
  }
  
  .error {
    color: rgb(243, 85, 85);
    font-size: 12px;
  }
  
  .title {
    font-size: 25px;
    margin-bottom: 20px;
    color: #ffffff;
  }
  
  .input-container {
    display: grid;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
    margin: 10px;
  }

  p{
    color: #ffa2a2;
  }

